import React, { Component } from 'react';
import Home from './Home';
import Board from './Board'
export default class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            loaded: false,
            boardName: '',
        }
        this.setLocal= this.setLocal.bind(this);
    }
    componentDidMount() {
        let board = localStorage.getItem('board')
        if (board) {
            this.setState({loaded: true, boardName: board})
        }
    }

    setLocal(boardName) {
        localStorage.setItem('board', boardName)
        this.setState({loaded: true, boardName: boardName})
    }

    render() {
        if (this.state.loaded) {
            return (
                <Board board={this.state.boardName}/>
            )
        } else {
            return(
                <Home setLocal={this.setLocal}/>
            )
        }
    }
}



