import React, {Component} from 'react';
import 'whatwg-fetch'

export default class Board extends Component {
    constructor(props) {
        super(props)

        
    }
    componentDidMount() {
        fetch(`/api/board?board=${this.props.board}`)
        .then((res) => res.json())
        .then((data) => {
            console.log(data)
        })
    }
    render() {
        return (
            <div>
                <h1>{this.props.board}</h1>
            </div>
        )
    }
}


